import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AlertController, ModalController } from '@ionic/angular';
import { MessageService } from 'primeng/api';
import { GenericHallDTO } from 'src/app/models/hall/generic-hall.dto';
import { ItemService } from 'src/app/services/criacoes/item/item.service';
import { DetalharService } from 'src/app/services/hall/detalhar/detalhar.service';
import { ItemPageService } from 'src/app/services/page/item/item-page.service';
import { StorageService } from 'src/app/services/storage-service.service';
import { PresentAtaqueComponent } from '../present-ataque/present-ataque.component';

@Component({
  selector: 'app-item-store-defesa',
  templateUrl: './item-store-defesa.component.html',
  styleUrls: ['./item-store-defesa.component.scss'],
})
export class ItemStoreDefesaComponent implements OnInit {

  private loading = false;
  private abaAtual = 'me';

  private projecoes: GenericHallDTO[] = [];
  private pageSize = 0;

  private linhasPorPagina = 20;

  private descricao = '';
  private orderBy = 'dataCriacao';
  private direction = 'DESC';
  private flagSearched = false;

  private xpOperationMenssage;
  private selectedItem;

  @Output()
  notifyParent: EventEmitter<any> = new EventEmitter();
  sendOperation(isDebitado: boolean = false) {
    const operation = {
      valor: this.selectedItem.valor,
      item: this.selectedItem,
      isDebitado
    }
    this.notifyParent.emit(operation);
    return operation;
  }

  constructor(
    private auth: StorageService,
    private modal: ModalController,
    private modalInterno: ModalController,
    private detalharService: DetalharService,
    private toast: MessageService,
    private itemService: ItemService,
    private itemPageService: ItemPageService,
  ) {
    this.byMe('0');
  }

  ngOnInit() {

  }

  isLogedIn() {
    return this.auth.isLogedIn();
  }

  mudaAba(event) {
    //console.log(this.pageSize);
    const { value } = event.detail;
    if (value === 'search')
      this.flagSearched = false;
    else if (value === 'me')
      this.byMe('0');
    else
      this.consultar('0');
  }

  byMe(page: string) {
    this.loading = true;
    this.itemService.findPageOfDefesa(page, this.linhasPorPagina.toString())
      .subscribe(res => {
        const { content, totalElements } = res;
        this.projecoes = content.map((arr) => this.convertToGenericHall(arr));
        this.pageSize = totalElements;
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        });
    this.loading = false;
  }

  consultar(page: string) {
    this.loading = true;
    this.itemPageService.findPageByNewerDefesa(page, this.linhasPorPagina.toString())
      .subscribe(res => {
        const { content, totalElements } = res;
        this.projecoes = content;
        this.pageSize = totalElements;
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        });
  }

  pesquisar(page: string) {
    this.loading = true;
    this.itemPageService.findPageBySearchDefesa(page, this.descricao, this.orderBy, this.direction, this.linhasPorPagina.toString())
      .subscribe(res => {
        const { content, totalElements } = res;
        this.projecoes = content;
        this.pageSize = totalElements;
        this.loading = false;
        this.flagSearched = true;
      },
        error => {
          if (error.status == 404)
            this.handle404(error);
          else
            console.log(error);

          this.loading = false;
        })
  }


  paginar(evt) {
    const { page } = evt;
    this.consultar(page.toString());
  }

  async useItem(projecao: GenericHallDTO) {
    this.loading = true;
    const item = await this.itemService.findById(projecao.id, '1').toPromise();
    this.selectedItem = item;
    this.loading = false;
    this.confirmacao();
  }

  handle404(e) {
    this.flagSearched = false;
    this.callToast('warn', 'Famoso 404', 'Infelizmente no encontramos nada com essa palavra')
  }

  callToast(severity, summary, detail, life = 3000) {
    this.toast.clear();
    this.toast.add({
      severity,
      summary,
      detail,
      life
    });
  }

  async showDetail(projecao: GenericHallDTO) {
    this.loading = true;
    const item = await this.itemService.findById(projecao.id, '1').toPromise();
    this.loading = false;

    const modal = await this.modalInterno.create({
      component: PresentAtaqueComponent,
      componentProps: {
        item,
        fullPresentation: true,
      },
      backdropDismiss: false

    });

    return await modal.present();

  }

  convertToGenericHall(item) {
    const { nome, descricao, tipoRPG, imgURL, idItem } = item;
    const genericItem: GenericHallDTO = {
      nome,
      descricao,
      imgURL,
      id: idItem,
    }
    return genericItem;
  }

  close() {
    this.modal.dismiss();
  }

  confirmacao() {
    this.toast.clear();
    this.xpOperationMenssage = `Deseja debitar o valor de <strong>${this.selectedItem.valor}</strong> do inventrio? 
    <br>Se voc clicar em 'no', o item ser adicionado sem qualquer custo`;
    this.toast.add({ key: 'c', sticky: true, severity: 'warn', summary: 'Are you sure?', detail: '' });
  }

  onConfirm() {
    this.toast.clear();
    const data = this.sendOperation(true);
    this.modal.dismiss(data);
  }

  onReject() {
    this.toast.clear();
    const data = this.sendOperation(false);
    this.modal.dismiss(data);
  }

  onCancel() {
    this.toast.clear();
  }

}
